<template>
  <el-select v-model="value" class="m-2 sel" placeholder="Select" size="large">
    <el-option
      v-for="item in options"
      :key="item.value"
      :label="item.label"
      :value="item.value"
    />
  </el-select>
  <div>
    <el-button class="btn" type="danger" @click="handleClick">ROLL</el-button>
  </div>
  <div>
    <p>我们今天吃:</p>
    <p class="res">{{results}}</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      options: [
        {
          value: "1",
          label: "米",
        },
        {
          value: "2",
          label: "面",
        },
        {
          value: "3",
          label: "麦",
        },
        {
          value: "4",
          label: "都行",
        },
      ],
      value: "",
      results: "",
    };
  },
  methods: {
        handleClick() {
            var rice = ["永和豆浆", "食其家", "谷田稻香", "伊鲜面馆", "香叶", "灶美味", "稻香情"];
            var wheat = ["大鼓", "伊鲜面馆", "和府捞面", "大秦府"];
            var all = ["永和豆浆", "食其家", "谷田稻香", "伊鲜面馆", "香叶", "灶美味", "稻香情", "大鼓", "麦当劳", "和府捞面", "大秦府"];

            if (this.value == "1"){
                this.results = rice[Math.floor(Math.random()*rice.length)];
            }
            if (this.value == "2"){
                this.results = wheat[Math.floor(Math.random()*wheat.length)];
            }
            if (this.value == "3"){
                this.results = "可以不用选的";
            }
            if (this.value == "4"){
                this.results = all[Math.floor(Math.random()*all.length)];
            }
            

        },
    },
};


</script>

<style>
.btn{
    margin-top: 10px;
}

p{
    font-size: large;
    font-weight: 800;
}

.res{
    font-size: 30 px;
    color: brown;
    padding-bottom: 80%;
}

.sel{
    padding-top: 15%;
}
</style>